import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import Image from "../components/image"
import SEO from "../components/seo"

import { CardDeck, Card } from "react-bootstrap" 


const CoursesPage = () => (
  <Layout>
    <SEO title="Courses" />
    <div class="container">
	    <h1>Courses</h1>
	    <p>Welcome to page 2</p>

	    <CardDeck>
		  <Card>
		    <Image /> 
		    <Card.Body>
		      <Card.Title>ML101</Card.Title>
		      <Card.Text>
		        Machine Learning | Autumn 2019
		      </Card.Text>
		    </Card.Body>
		    <Card.Footer>
		      <small className="text-muted">Last updated 3 mins ago</small>
		      <Link className="nav-link" to="./ml101/">ML101</Link>
		    </Card.Footer>
		  </Card>
		  <Card>
		    <Image />
		    <Card.Body>
		      <Card.Title>DF101</Card.Title>
		      <Card.Text>
		        Intro to Digital Fabrication | Autumn 2019
		    
		      </Card.Text>
		    </Card.Body>
		    <Card.Footer>
		      <small className="text-muted">Last updated 3 mins ago</small>
		      <Link className="nav-link" to="./df101/">DF101</Link>
		    </Card.Footer>
		  </Card>
		</CardDeck>
	</div>
  </Layout>
)

export default CoursesPage
